export default function Page() {
  const [show, setShow] = useState(false);
  const [currentCourse, setCurrentCourse] = useState(0);

  const setCourse = (course) => {
    setCurrentCourse(course)
  }

  return (
    <Container>
      <CardColumns>
        {miniatures.map((m, i) => (
          <Miniature classes="miniature shadow-drop-center" miniature={m} setCourse={setCourse} setShow={setShow} i={i}></Miniature>
        ))}
      </CardColumns>
      <Button variant="primary" onClick={() => setShow(true)}>
        Custom Width Modal
      </Button>

      <Modal
        show={show}
        onHide={() => setShow(false)}
        dialogClassName="modal-90w"
        aria-labelledby="example-custom-modal-styling-title"
        className="course-modal"
      >
        <Modal.Header closeButton>
          <Modal.Title id="example-custom-modal-styling-title">
            {miniatures[currentCourse].title}
          </Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <p>
            {miniatures[currentCourse].description}
          </p>
          {miniatures[currentCourse].images && <img src={miniatures[currentCourse].images[0]} alt="not importatnt " /> }
          <p>Umiejętności:</p>
          <p>
          <ul>
          {miniatures[currentCourse].skills.map(s => <li>{s}</li>)}
          </ul>
        </p>
        </Modal.Body>
      </Modal>
    </Container>
  );
}
