import React from "react";

import { Card, ListGroup } from "react-bootstrap";

import './miniature.styles.scss';
export default function Miniature({ miniature, setCourse, i, setShow, classes }) {
  const { image, title, description, skills } = miniature;
  return (
    <React.Fragment>
      <Card border="warning" className={classes} style={{ width: "18rem" }} onClick={() => {setCourse(i); setShow(true);}}>
        <Card.Img variant="top" src={image} />
        <Card.Header className="course-title"><b>{title}</b></Card.Header>
        <Card.Body>
          <Card.Text>{description.slice(0,100)}...</Card.Text>
        </Card.Body>
        <Card.Header>
          <ListGroup variant="flush">
            {skills.slice(0, 4).map((s) => (
              <ListGroup.Item className="skill-list">{s}</ListGroup.Item>
            ))}
          </ListGroup>
        </Card.Header>
      </Card>
      <br />
    </React.Fragment>
  );
}
